}

@media (max-width:768px){ 
    .webTitleDiv,.mainDiv{
    display: none;    
    }    
    .title{
        font-size: 30px;
    }


    .burgerBtn,.mediaBar,.mediaMainDiv{
        display: block;
    }


}



.mediaCartLogo{
    width: 40px;
    height: 40px;
    margin-top: 20px;
    margin-right: 10px;
    mix-blend-mode: multiply;
}


.mediaLogo{
    width: 130px;
    height: 70px;
    margin-top: 10px;
    mix-blend-mode: multiply;   
}

.humburger{
    width: 90px;
    right: 0;
    display: flex;
    flex-direction: column;
    border: 1px solid white;
    background-color: black;
    height: 300px;
    position: absolute;
    padding-right:10px;
   
  }
  
  .humburgerBtn{
    font-size: 15px;
    background-color: black;
    border: none;
    color: white;
    margin-top: 40px;
  }

  .humburger:active{
    text-decoration: underline;
  }

  .supriseBtn{
    width: 220px;
    height: 50px;
    font-size: 30px;
    border-radius: 20px;
    background-color:#adcbd3;
    font-family: "Times New Roman", Times, serif;
  }

  .cartDiv{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content:center
  }

  .chosenProductDiv{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 70%;
    margin: 0 auto;
    margin-top: 20px;
    border: 2px solid black;
    background-color: white;
    padding-right: 30px;
    padding-left: 30px; 
    padding-top: 30px;

}

